
import { HttpModule, XHRBackend, RequestOptions } from '@angular/http';
import { HttpRequestService } from './_common/http-request.service';
import { ChartService } from './_services/chart.service';

import { AngularReduxRequestOptions } from './_common/angular-redux-request-options';
import { LoaderComponent } from './_common/loader/loader.component';
import { ConfirmComponent } from './_common/confirm/confirm.component';
import { AlertBoxComponent } from './_common/alert-box/alert-box.component';
import { LoaderService } from './_common/loader/loader.service';
import { httpServiceFactory } from './_factories/http-service-factory';

import { BrowserModule } from '@angular/platform-browser';
import { NgModule, LOCALE_ID } from '@angular/core';
import { NvD3Module } from 'ng2-nvd3';

import { routing } from './app.routing';

import { AppComponent } from './app.component';
import { BarchartComponent } from './shared/barchart/barchart.component';
import { PieChartComponent } from './shared/pie-chart/pie-chart.component';


// d3 and nvd3 should be included somewhere
import 'd3';
import 'nvd3';
import { LineChartComponent } from './shared/line-chart/line-chart.component';
import { DonutChartComponent } from './shared/donut-chart/donut-chart.component';

@NgModule({
  declarations: [
    LoaderComponent,
    ConfirmComponent,
    AlertBoxComponent,
    AppComponent,
    BarchartComponent,
    PieChartComponent,
    LineChartComponent,
    DonutChartComponent
  ],
  imports: [
    routing,
    BrowserModule,
    NvD3Module,
    HttpModule
  ],
  providers: [
    LoaderService,
    HttpRequestService,
    ChartService,
    {
      provide: HttpRequestService,
      useFactory: httpServiceFactory,
      deps: [XHRBackend, RequestOptions, LoaderService]
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



